import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useLibraryRemoveBookReview } from "../../../Hooks/ApiHooks/LibraryManager/library-manager-api";
import ModalMenu from "../../../Ui/Modal/ModalMenu";

const BookReviewMenu = ({ bid, sid, onRefetch, onClose }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [libraryRemoveBookReview] = useLibraryRemoveBookReview();

  const onDelete = () => {
    if (bid && sid) {
      setDisabled((pre) => !pre);
      libraryRemoveBookReview({
        bid: bid,
        sid: sid,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default BookReviewMenu;
